@keyframes swing {
    0% {
      transform: translateX(-150px) rotate(3deg);
    }
    100% {
      transform: translateX(-150px) rotate(-3deg);
    }
  }

.topbar {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 4%;
    box-sizing: border-box;
    flex-wrap: wrap;
}

.topbarleftalign {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100%;
}

.logo {
    background-image: url(./media/logo.svg);
    width: 80px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 80px;
}

.logostaking {
    font-weight: 800;
    font-size: 30px;
    user-select: none;
    color: white;
}

.connectwalletbutton {
    height: 50px;
    background-color: #fdca14;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
    padding: 0 25px;
    font-weight: 700;
    border-radius: 12px;
    position: absolute;
    top: 0px;
    right: 20px;
    color: black;
}

.connectwalletbuttonactive {
    background-color: #55d155;
    color: white;
}

.contentcontainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 25px;
    width: 100%;
}

.infotext {
    margin-top: 4%;
    font-weight: 800;
    text-align: center;
    font-size: 20px;
    color: white;
}

.buyinputcontainer {
    width: 500px;
    background-color: rgba(255, 255, 255, 0.4);;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 14px;
    margin-top: 100px;
    display: flex;
    align-items: center;
    padding: 30px 0;
    flex-direction: column;
}

.buyinputelementcontainer{
    width: 90%;
    position: relative;
    margin: 15px 0;
}

.buyinputelement{
    width: 100%;
    height: 60px;
    background-color: #C0C0C0;
    border: none;
    border-radius: 10px;
    font-weight: 700;
    font-size: 30px;
    padding-left: 10px;
    background-color: white;
    box-sizing: border-box;
}

.buyinputlabel{
    position: absolute;
    top: 0px;
    right: 15px;
    font-weight: 700;
    font-size: 20px;
}

.purpleflokilabel{
    text-align: right;
}

.buybutton{
    margin: 15px 0 5px 0;
    border-radius: 10px;
    background-color: #55d155;
    padding: 15px 70px;
    font-weight: 700;
    font-size: 20px;
    cursor: pointer;
}

.hang{
    width: 170px;
    height: 200px;
    position: absolute;
    z-index: -1;
    bottom: -120px;
    background-image: url(./images/hang.png);
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    animation: swing ease-in-out 1s infinite alternate;
}

@media only screen and (max-width: 750px) {
    .connectwalletbutton {
        transform: translateY(70px);
    }
    .infotext {
        transform: translateY(-65px);
    }
}

@media only screen and (max-width: 500px) {
    .buyinputcontainer {
        width: 95%;
    }
}

@media only screen and (max-width: 800px) {
    .logostaking {
        font-size: 18px;
    }
}

.connectwalletbutton {
    margin-top: 100px;
}

.topbar {
    margin-bottom: 100px;
}